import React, { Component, Fragment } from "react";
import "./Styles/Welcome.css"
import video from "./assets/videoBackground.mp4";
import { updateRenderState } from ".";
import { S, wait, toEnglishNumber, Snackbar, snackbarHandle } from "./dependencies";
import { userData, storeData } from "./Data";
import birthdayCake from "./assets/birthdayCake.png";
import measureTape from "./assets/measureTape.png";
import scale from "./assets/scale.png";
import trophy from "./assets/trophy.png";

import MainPage from "./NavicationBar/MainPage";
import NavicationBar from "./NavicationBar/NavicationBar";
import Loading from "./Loading";
import MainPageLoading from "./MainPageLoading";

let isGuest;
export default class Welcome extends Component {
  state = {
    isVideoLoaded: false,
    renderLoading: "",
  };

  componentDidMount() {
    this.checkVideoLoad();

    let tl = setInterval(() => {
      if (document.getElementById("firebaseui-auth-container").hasChildNodes()) {
        this.addGuestButton();
        clearInterval(tl);
      }
    }, 200);
  }

  addGuestButton = async () => {
    const cloneString = "#firebaseui-auth-container > div div:nth-child(1) > form > ul li:nth-child(1)";
    const spanString = cloneString + " > button span:nth-child(2)";
    const imgString = cloneString + " > button span:nth-child(1) > img";
    const prependString = "#firebaseui-auth-container > div div:nth-child(1) > form > ul";
    const clone = document.querySelectorAll(cloneString)[0].cloneNode(true);
    document.querySelectorAll(prependString)[0].prepend(clone);
    S(cloneString + " > button")
      .css("background-color", "#d0ad3e")
      .on("click", this.guestHandle);
    S(spanString).css("color", "#fff").text("المتابعة بدون تسجيل دخول");
    S(imgString).attr("src", require("./assets/user.png"));
  };

  guestHandle = () => {
    isGuest = true;
    this.scrollHandle();
  };

  scrollHandle = () => {
    const el = document.getElementById("welcomePagesContainer");
    const getWidth = parseFloat(window.getComputedStyle(el).getPropertyValue("width"));
    const pos = el.scrollLeft;
    let count = 0;
    const tl = () => {
      count = Math.min((count += 60), getWidth);
      el.scrollTo({ left: pos - count });
      if (count < getWidth) {
        window.requestAnimationFrame(tl);
      }
    };
    window.requestAnimationFrame(tl);
  };

  backButtonHandle = () => {
    const el = document.getElementById("welcomePagesContainer");
    const getWidth = parseFloat(window.getComputedStyle(el).getPropertyValue("width"));
    const pos = el.scrollLeft;
    let count = 0;

    const tl = () => {
      count = Math.min((count += 60), getWidth);
      el.scrollTo({ left: pos + count });
      if (count < getWidth) {
        window.requestAnimationFrame(tl);
      }
    };
    window.requestAnimationFrame(tl);
  };

  validateForm = (e) => {
    const age = Number(document.getElementById("ageInput").value);
    const height = Number(document.getElementById("heightInput").value);
    const weight = Number(document.getElementById("weightInput").value);
    const goal = Number(document.getElementById("goalInput").value);

    if (age < 15 || age > 120) {
      snackbarHandle("الرجاء التحقق من العمر", "error", true);
      S("#ageInput").css("border-color", "#f44336");
    } else if (height < 91 || height > 360) {
      snackbarHandle("الرجاء التحقق من الطول", "error", true);
      S("#heightInput").css("border-color", "#f44336");
    } else if (weight < 20 || weight > 250) {
      snackbarHandle("الرجاء التحقق من الوزن", "error", true);
      S("#weightInput").css("border-color", "#f44336");
    } else if (goal < 20 || goal > 250) {
      snackbarHandle("الرجاء التحقق من الوزن الهدف", "error", true);
      S("#goalInput").css("border-color", "#f44336");
    } else {
      this.scrollHandle();
    }
    e.preventDefault();
  };

  activitySelectHandle = (e, n) => {
    userData.userInfo.activity = n;
    S(".activityContainer").removeAttr("style");
    e.target.style.cssText = `background-color: #b2cfff`;
  };

  doneHandle = async () => {
    if (userData.userInfo.activity.length === 0) {
      snackbarHandle("الرجاء اختيار مستوى نشاط", "error", true);
    } else {
      if (isGuest) window.localStorage.setItem("isGuest", "true");
      this.autoAssign();
      this.setState({ renderLoading: <MainPageLoading /> });
      this.scrollHandle();
      storeData();
      await wait(3000);
      updateRenderState([<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key={Math.random() * 100} />]);
    }
  };

  HarrisBenedictBMR = () => {
    let bmr;
    if (userData.userInfo.gender === "male") {
      bmr = 66.47 + 13.75 * userData.userInfo.weight + 5.003 * userData.userInfo.height - 6.755 * userData.userInfo.age;
    } else if (userData.userInfo.gender === "female") {
      bmr = 655.1 + 9.563 * userData.userInfo.weight + 1.85 * userData.userInfo.height - 4.676 * userData.userInfo.age;
    }
    return Math.round(Math.round(bmr) / 100) * 100;
  };

  autoAssign = () => {
    const bmr = this.HarrisBenedictBMR();
    const goal = userData.userInfo.goalWeight;
    const weight = userData.userInfo.weight;
    const activity = userData.userInfo.activity;

    let kcal;
    let carbs;
    let protien;
    let fat;

    if (goal < weight) {
      kcal = bmr;
      carbs = Math.round((kcal * 0.5) / 4);
      protien = Math.round((kcal * 0.25) / 4);
      fat = Math.round((kcal * 0.25) / 9);
    } else if (goal === weight) {
      kcal = Math.round(bmr * activity);
      carbs = Math.round((kcal * 0.55) / 4);
      protien = Math.round((kcal * 0.2) / 4);
      fat = Math.round((kcal * 0.25) / 9);
    } else if (goal > weight) {
      kcal = Math.round(bmr * activity + 500);
      carbs = Math.round((kcal * 0.55) / 4);
      protien = Math.round((kcal * 0.2) / 4);
      fat = Math.round((kcal * 0.25) / 9);
    }
    userData.userInfo.dailykcal = kcal;
    userData.userInfo.dailyCarbs = carbs;
    userData.userInfo.dailyProtein = protien;
    userData.userInfo.dailyFat = fat;
  };

  checkVideoLoad = () => {
    const videoElement = document.getElementById("welcomeVideo");
    videoElement.addEventListener("loadeddata", () => {
      let b = setInterval(() => {
        if (videoElement.readyState === 4) {
          this.setState({ isVideoLoaded: true });
          clearInterval(b);
        }
      }, 500);
    });
  };

  render() {
    return (
      <Fragment>
        {!this.state.isVideoLoaded ? <Loading /> : ""}

        <Snackbar />

        <div id="welcomePagesContainer" style={{ display: this.state.isVideoLoaded ? "block" : "none" }}>
          <div
            className="welcomePages"
            id="firstPage"
            style={{ height: window.innerHeight + "px", display: this.props.skipLogIn ? "none" : "inline-block" }}
          >
            <video id="welcomeVideo" autoPlay muted loop>
              <source src={video} type="video/mp4" />
            </video>
            <div id="welcomeVideoFilter"></div>
            <div className="welcomeGrade"></div>
            <p className="welcomePageSubTitle" style={{ top: "2%" }}>
              أهلاً وسهلاً
            </p>
            <p className="welcomePageTitle">سعراتي اليومية</p>
            <p
              className="welcomeNextButtons"
              onClick={async () => {
                this.scrollHandle();
                await wait(1000);
                S("#welcomeVideo").remove();
              }}
            >
              التالي
            </p>
          </div>

          <div
            className="welcomePages"
            id="loggInPage"
            style={{ height: window.innerHeight + "px", display: this.props.skipLogIn ? "none" : "inline-block" }}
          >
            <p className="welcomePageTitle">تسجيل الدخول</p>
            <div id="firebaseui-auth-container"></div>
          </div>

          <div className="welcomePages" id="secondPage" style={{ height: window.innerHeight + "px" }}>
            <svg
              className="welcomeBackButtons"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              style={{ display: this.props.skipLogIn ? "none" : "block" }}
              onClick={this.backButtonHandle}
            >
              <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
            </svg>
            <p className="welcomePageTitle">مرحباً ! </p>
            <p className="welcomePageSubTitle">لنبدأ بالمعلومات الأساسية</p>
            <div id="genderContainer">
              <p
                id="male"
                onClick={(e) => {
                  userData.userInfo.gender = e.target.id;
                  this.scrollHandle();
                }}
              >
                ذكر
              </p>
              <p
                id="female"
                onClick={(e) => {
                  userData.userInfo.gender = e.target.id;
                  this.scrollHandle();
                }}
              >
                انثى
              </p>
            </div>
          </div>

          <div className="welcomePages" id="thirdPage" style={{ height: window.innerHeight + "px" }}>
            <svg
              className="welcomeBackButtons"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              onClick={this.backButtonHandle}
            >
              <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
            </svg>

            <form className="welcomeInputWrapper" onSubmit={this.validateForm}>
              <div className="welcomeInputContainer">
                <label htmlFor="ageInput">عمرك</label>
                <input
                  id="ageInput"
                  type="text"
                  autoComplete="off"
                  inputMode="numeric"
                  placeholder="العمر"
                  onClick={(e) => e.target.select()}
                  onFocus={(e) => e.target.removeAttribute("style")}
                  onChange={(e) => {
                    e.target.value = toEnglishNumber(e.target.value);
                    userData.userInfo.age = Number(e.target.value);
                  }}
                  required
                />
                
                <div id="birthdayCake" className="unitsContainer">
                  <p>سنة</p>
                  <img src={birthdayCake} alt="birthdayCake" />
                </div>
              </div>
              <div className="welcomeInputContainer">
                <label htmlFor="heightInput">طولك</label>
                <input
                  id="heightInput"
                  name="heightInput"
                  type="text"
                  autoComplete="off"
                  inputMode="numeric"
                  placeholder="طولك بالسنتيمتر"
                  onClick={(e) => e.target.select()}
                  onFocus={(e) => e.target.removeAttribute("style")}
                  onChange={(e) => {
                    e.target.value = toEnglishNumber(e.target.value);
                    userData.userInfo.height = Number(e.target.value);
                  }}
                  required
                />
                <div id="measureTape" className="unitsContainer">
                  <p>سنتيمتر</p>
                  <img src={measureTape} alt="measureTape" />
                </div>
              </div>
              <div className="welcomeInputContainer">
                <label htmlFor="weightInput">وزنك</label>
                <input
                  id="weightInput"
                  type="text"
                  autoComplete="off"
                  inputMode="numeric"
                  placeholder="وزنك بالكيلوغرام"
                  onClick={(e) => e.target.select()}
                  onFocus={(e) => e.target.removeAttribute("style")}
                  onChange={(e) => {
                    e.target.value = toEnglishNumber(e.target.value);
                    userData.userInfo.weight = Number(e.target.value);
                  }}
                  required
                />
                
                <div id="scale" className="unitsContainer">
                  <p>كيلوغرام</p>
                  <img src={scale} alt="scale" />
                </div>
              </div>
              <div className="welcomeInputContainer">
                <label htmlFor="goalInput">الوزن الهدف</label>
                <input
                  id="goalInput"
                  type="text"
                  autoComplete="off"
                  inputMode="numeric"
                  placeholder="الهدف بالكيلوغرام"
                  onClick={(e) => e.target.select()}
                  onFocus={(e) => e.target.removeAttribute("style")}
                  onChange={(e) => {
                    e.target.value = toEnglishNumber(e.target.value);
                    userData.userInfo.goalWeight = Number(e.target.value);
                  }}
                  required
                />
                
                
                <div id="trophy" className="unitsContainer">
                  <p>كيلوغرام</p>
                  <img src={trophy} alt="trophy" />
                </div>
              </div>
              <input type="submit" value="التالي" className="welcomeNextButtons" style={{ bottom: "-23%" }} />
            </form>
          </div>

          <div className="welcomePages" id="forthPage" style={{ height: window.innerHeight + "px" }}>
            <svg
              className="welcomeBackButtons"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              onClick={this.backButtonHandle}
            >
              <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
            </svg>

            <div id="activityWrapper">
              <p>اختر مستوى نشاطك</p>
              <div className="activityContainer" onClick={(e) => this.activitySelectHandle(e, 1.2)}>
                <p>كثير الجلوس</p>
                <p>نشاط قليل من دون أي تمارين رياضية كالعمل في مكتب</p>
              </div>
              <div className="activityContainer" onClick={(e) => this.activitySelectHandle(e, 1.375)}>
                <p>قليل النشاط</p>
                <p>تمارين رياضية بسيطة 1-3 أيام بالأسبوع</p>
              </div>
              <div className="activityContainer" onClick={(e) => this.activitySelectHandle(e, 1.55)}>
                <p>متوسط النشاط</p>
                <p>تمارين رياضية متوسطة 6-7 أيام بالأسبوع</p>
              </div>
              <div className="activityContainer" onClick={(e) => this.activitySelectHandle(e, 1.725)}>
                <p>كثير النشاط</p>
                <p>تمارين صعبة كل يوم</p>
              </div>
              <div className="activityContainer" onClick={(e) => this.activitySelectHandle(e, 1.9)}>
                <p>عالي النشاط</p>
                <p>تمارين صعبة مرتين أو أكثر كل يوم</p>
              </div>
            </div>
            <p className="welcomeNextButtons" onClick={this.doneHandle}>
              تم
            </p>
          </div>
          <div
            className="welcomePages"
            id="fifthPage"
            style={{ height: window.innerHeight + "px", backgroundcolor: "var(--background-gray)", background: "initial" }}
          >
            {this.state.renderLoading}
          </div>
        </div>
      </Fragment>
    );
  }
}
